.about-grid {
  width: 100%;
  display: grid;
  grid-template-columns: auto;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem 5rem;

  background: #fff;
  color: #fff;
  letter-spacing: 0.2rem;
  overflow: hidden;
}

.about-grid .item {
  display: flex;
  align-items: center;
  width: 100%;
  gap: 1rem;
}

.about-grid .item div {
  box-sizing: border-box;
  width: 50%;
  overflow: hidden;
  box-shadow: inset;
}

.about-grid .item div img {
  transition: all 500ms;
  max-width: 100%;
  border-radius: 10px;
}

.about-grid .item div img:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.about-grid .item:nth-child(1) {
  flex-direction: column;
  padding: 1rem;
}

.about-grid .item:nth-child(4) {
  flex-direction: column;
  padding: 1rem;
}

.about-grid .item:nth-child(4) img {
  filter: contrast(75%);
}

.about-grid .item .txt-box {
  color: var(--txt-color);
}

@media only screen and (max-width: 868px) {
  .about-grid {
    letter-spacing: 0.1rem;
  }
  .about-grid .item {
    flex-direction: column;
    padding: 1rem;
  }
  .about-grid .item:nth-child(3) {
    flex-direction: column-reverse;
  }
  .about-grid .item div {
    width: 100%;
  }
}
